.cases {
    position: relative;
    height: 50vh;
    height: calc(var(--vh, 1vh) * 50);
    .cases-navigation {
        position: absolute;
        bottom: 0;
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        z-index: 6;
        padding: 32px;
        box-sizing: border-box;
        @include media('<=tablet') {
            display: none;
        }
        .cases-arrow {
            background: rgba(0,0,0,0.4);
            height: 50px;
            width: 50px;
            border-radius: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            cursor: pointer;
            svg {
                color: $white;
                height: 28px;
                width: 28px;
            }
            &.disabled {
                opacity: 0.3;
            }
        }
    }
    .row {
        @include media('<=tablet') {
            flex-direction: column;
        }
        .case {
            position: relative;
            cursor: pointer;
            background: $black;
            &:hover {
                .case-image {
                    opacity: 0.4;
                }
            }
            .case-details {
                width: 33.333vw;
                height: 50vh;
                height: calc(var(--vh, 1vh) * 50);
                display: flex;
                flex-direction: column;
                padding: 32px;
                box-sizing: border-box;
                z-index: 1;
                position: relative;
                @include media('<=tablet'){
                    width: 100vw;
                }
                @include media('<=phone') {
                    padding: 16px;
                }
                span {
                    margin-top: 130px;
                    font-size: 1.4rem;
                    opacity: 0.8;
                    color: $white;
                    font-weight: 600;
                    @include media('<=desktop', '>tablet') {
                        font-size: 1.4rem;
                    }
                    @include media('<=phone') {
                        font-size: 1.2rem;
                        line-height: 2.2rem;
                    }
                }
                h2 {
                    font-size: 2rem;
                    line-height: 3.4rem;
                    width: 85%;
                    margin-top: 10px;
                    color: $white;
                    @include media('<=desktop', '>tablet') {
                        font-size: 1.7rem;
                        line-height: 2.4rem;
                    }
                    @include media('<=phone') {
                        font-size: 1.5rem;
                        line-height: 2.2rem;
                        margin-top: 8px;
                    }
                }
            }
            .case-image {
                top: 0;
                left: 0;
                right: 0;
                height: 100%;
                width: 100%;
                overflow: hidden;
                position: absolute;
                opacity: 0.65;
                transition: 0.4s cubic-bezier(0.6, -0.5, 0.1, 0.99);
                img {
                    height: 100%;
                    width: 100%;
                    object-fit: cover;
                }
            }
        }
    }
}